.card-well {
  opacity: 0;
  border-radius: 4px;
  box-sizing: border-box;
  width: 50px;
  height: 80px;
  background: yellowgreen;
}

.card-well--can-drop {
  opacity: 1;
  background: blue;
}

.card-well--can-not-drop {
  opacity: 1;
  background: red;
}

.card-well:not(.card-well--clickable) {
  pointer-events: none;
}